<template>
    <div class="detail-presell">
        <div>
            <van-swipe class="swiper"
                       :autoplay="0"
                       :show-indicators=false
                       indicator-color="white"
                       @click="$isApp"
            >
                <van-swipe-item>
                    <img class="showImg" :src="list.arrayImgUrl" alt="">
                </van-swipe-item>
            </van-swipe>
            <div class="presell-title">
                <span class="star" v-if="list.singername">{{list.singerName}}</span>
                <span class="t">{{list.title}}</span>
            </div>
            <div class="presell-value">
                <span class="cost">¥{{list.priceInterval}}</span>
                <em class="tags" v-if="list.isRestriction!==0">限购</em>
                <em class="tags" v-if="list.isFree===1">包邮</em>
                <i class="selled">已售 {{list.soldNum}} 件</i>
            </div>
            <p class="tips" v-if="list.status===0">预售未开始</p>
            <p class="tips" v-if="list.status===1">预售时间：{{formatTime(new Date(list.presaleStartTime),':',1)}} --
                {{formatTime(new Date(list.presaleEndTime),':',1)}}</p>
            <p class="tips" v-if="list.status===2">预售时间：{{formatTime(new Date(list.presaleStartTime),':',1)}} --
                {{formatTime(new Date(list.presaleEndTime),':',1)}}</p>
            <p class="tips" v-if="list.status===3">支付尾款时间：{{formatTime(new Date(list.balanceStartTime),':',1)}} --
                {{formatTime(new Date(list.balanceEndTime),':',1)}}</p>
            <p class="tips" v-if="list.status===4">立即补款结束</p>
            <p class="blue-tips">未成年人请在监护人同意下使用平台服务</p>
            <div class="follow-sth" @click="$isApp">
                <img class="follow-head" :src="list.userHomePage.headUrl" alt="">
                <div class="follow-nickname">{{list.userHomePage.account}}</div>
                <div class="follow-btn" :class="{'gray-font':list.userHomePage.isConcern===1}">
                    {{list.userHomePage.isConcern===0?'+关注':'已关注'}}
                </div>
                <div class="fans-num">粉丝：{{list.userHomePage.fansNum}}</div>
            </div>
            <van-cell class="should-know" to="/notePresell" title="预售补款须知" is-link/>
            <div class="img-box">
                <img class="showImg" :src="item" v-for="item in list.imgUrl" alt="">
            </div>
            <div class="footer-btn">
                <van-button class="opreate-btn" type="primary" size="large" @click="$isApp"
                            :class="{grayColor:changeGray}">{{buttonText}}
                </van-button>
            </div>
        </div>
        <div class="tips" v-if="list.status===5"><img class="default-img" src="../../assets/images/no-goods.png" alt="">
        </div>
    </div>
</template>

<script>
  import formatTime from '../../utils/formatTime'

  export default {
    name: "DetailPresell",
    data() {
      return {
        buttonText: '',
        changeGray: false,
        list: {
          addUserId: 0,
          arrayImgUrl: "",
          balanceEndTime: 0,
          balanceStartTime: 0,
          id: 1,
          imgUrl: "",
          isFree: 0,
          isRestriction: 0,
          presaleEndTime: 0,
          presaleStartTime: 0,
          price: '00.00',
          priceInterval: "",
          realPrice: '00.00',
          soldNum: 0,
          status: 1,
          title: "",
          userHomePage: {
            account: "",
            autograph: "@",
            birthday: 0,
            fansNum: 0,
            followNum: 0,
            headUrl: "",
            id: 0,
            integral: 0,
            isConcern: 0,
            phoneNum: "",
            qq: 0,
            sex: 1,
            teamId: 0,
            todayIntegral: 0,
            userId: 0,
            userLvl: 0,
            userStatus: 0,
            userType: 0,
            vipStatus: 0,
            weibo: 0,
            weixin: 0
          }
        }
      }
    },
    created() {
      document.title = '预售详情';
    },
    mounted() {
      this.getData();
    },
    methods: {
      formatTime: formatTime,
      getData: function () {
        let params = this.$queryParams(location.href);
        // 预售详情
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE4 + '/GoodsAPI/presaleDetails',// 预售详情
          data: this.$qs.stringify({
            id: params.id,
            userId: params.userId
          })
        }).then((res) => {
            if (res.data.status !== 1000) {
              // this.$toast(res.data.msg);
              return false;
            } else if (res.data.data !== null) {
              this.list = res.data.data;
              this.list.imgUrl = this.list.imgUrl.split(',');
              this.deadline();
            }
          }
        ).catch((err) => {
          console.log(err);
        })
      },
      deadline: function (t) {
        let date = new Date().getTime();

        if (this.list.status === 0) {
          this.buttonText = '未开始';
          this.changeGray = true;
        }
        if (this.list.status === 1) {
          this.buttonText = '立即购买';
        }
        if (this.list.status === 2) {
          this.buttonText = '预售结束';
          this.changeGray = true;
        }
        if (this.list.status === 3) {
          this.buttonText = '立即补款';
        }

        if (this.list.status === 4) {
          this.buttonText = '立即补款结束';
          this.changeGray = true;
        }
        if (this.list.status === 5) {
          this.buttonText = '下架';
          this.changeGray = true;
        }
      }
    }
  }
</script>

<style scoped lang="scss">
    .img-box {
        padding-bottom: 1rem;
        font-size: 0;
    }

    .default-img {
        width: 100%;
    }

    .grayColor {
        background: #c7c1c1 !important;
    }

    .gray-font {
        border: .02rem solid gray !important;
        color: gray !important;
    }

    .showImg {
        width: 100%;
    }

    .detail-presell {
        height: 100%;
        width: 100%;
        .swiper {
            width: 100%;
            overflow: auto;
        }
        .presell-title {
            color: #000;
            font-weight: 600;
            /*max-height: 1.1rem;*/
            line-height: .4rem;
            padding: .2rem;
            background: #fff;
            /*overflow: hidden;*/
            /*-ms-text-overflow: ellipsis;*/
            /*text-overflow: ellipsis;*/
            /*display: -webkit-box;*/
            /*-webkit-box-orient: vertical;*/
            /*-webkit-line-clamp: 2;*/
            .t {
                font-size: .28rem;
                vertical-align: middle;
            }
            .star {
                padding: 0 .08rem;
                background: #FF2E4D;
                text-align: center;
                font-size: .26rem;
                color: #fff;
                -webkit-border-radius: .14rem;
                -moz-border-radius: .14rem;
                border-radius: .14rem;
            }
        }
        .presell-value {
            width: 100%;
            height: .5rem;
            padding: 0 .2rem;
            background: #fff;
            .cost {
                height: 100%;
                color: #FF2E4D;
                font-size: .36rem;
                line-height: .55rem;
                display: block;
                float: left;
            }
            .tags {
                display: block;
                line-height: .32rem;
                height: .3rem;
                margin: .1rem .1rem 0 .1rem;
                -webkit-border-radius: .04rem;
                -moz-border-radius: .04rem;
                border-radius: .04rem;
                border: 1px solid #909090;
                font-size: .2rem;
                color: #909090;
                float: left;
                padding: 0 .05rem;
            }
            .selled {
                color: #909090;
                font-size: .24rem;
                float: right;
                display: block;
                line-height: .55rem;
                height: 100%;
            }
        }
        .tips {
            width: 100%;
            height: .87rem;
            line-height: .3rem;
            padding: .27rem .2rem .3rem .2rem;
            font-size: .24rem;
            color: #909090;
            background: #fff;
        }
        .blue-tips {
            background: #fff;
            padding: 0 .2rem .4rem .2rem;
            font-size: .22rem;
            color: #458AFC;
        }
        .follow-sth {
            height: 1.37rem;
            width: 100%;
            padding: 0 .2rem;
            background: #fff;
            margin: .32rem 0;
            overflow: hidden;
            .follow-head {
                height: .71rem;
                width: .71rem;
                margin: .33rem 0;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                border-radius: 50%;
                float: left;
            }
            .follow-nickname {
                height: 50%;
                line-height: 1rem;
                width: 5rem;
                float: left;
                font-size: .33rem;
                color: #333333;
                padding: 0 .15rem;
            }
            .fans-num {
                height: 50%;
                width: 5rem;
                line-height: .5rem;
                font-size: .22rem;
                color: #909090;
                padding: 0 .15rem;
                float: left;
            }
            .follow-btn {
                float: right;
                height: .56rem;
                width: 1rem;
                -webkit-border-radius: .1rem;
                -moz-border-radius: .1rem;
                border-radius: .1rem;
                border: .02rem solid #FF2E4D;
                color: #FF2E4D;
                line-height: .56rem;
                text-align: center;
                margin: .41rem 0;
            }
        }
        .should-know {
            color: #333333;
            font-size: .28rem;

            margin-bottom: .3rem;
        }
        .footer-btn {
            height: .98rem;
            width: 100%;
            background: #fff;
            margin: 0 auto;
            position: fixed;
            left: 0;
            bottom: 0;
            right: 0;
            border: 1px solid #F2F2F2;
            .opreate-btn {
                display: block;
                height: .7rem;
                border-radius: .35rem;
                line-height: .7rem;
                border: 0;
                font-size: .28rem;
                background: #FF2E4D;
                width: 5rem;
                margin: .14rem auto;
            }
        }
    }
</style>
